<template>
  <div class="test-pages pa-3">
    <v-layout column>
      <div class="wrapper-login pa-5">
        <div>
          <v-card-text class="avenir-14-reg mb-1 color-text-3">Email</v-card-text>
          <v-text-field
            v-model="email"
            outlined
            background-color="white"
            placeholder="Masukkan Email Kamu"
            dense
            fixed
            type="text"
            hide-details
          ></v-text-field>
          <div class="color-red avenir-12-reg pt-2" v-if="validation.email">{{ validation.email }}</div>
        </div>
        <div class="mt-5">
          <v-card-text class="avenir-14-reg mb-1 color-text-3">Username</v-card-text>
          <v-text-field
            v-model="username"
            outlined
            background-color="white"
            placeholder="Masukkan Username Kamu"
            dense
            fixed
            type="text"
            hide-details
          ></v-text-field>
        <!-- <div class="color-red avenir-12-reg" v-if="validation.email">{{ validation.email }}</div> -->
        </div>
        <div class="mt-5">
          <v-card-text class="avenir-14-reg mb-1 color-text-3">Password</v-card-text>
          <v-text-field
            v-model="password"
            :type="showPassword ? 'text' : 'password'"
            outlined
            background-color="white"
            placeholder="Masukkan Kata Sandi"
            dense
            hide-details
          >
          <div slot="append" @click="showPassword = !showPassword">
            <template v-if="showPassword">
              <img
                src="@/assets/generic/icons/ic-eye-off.svg"
                alt="eye-off"
                class="cursor-pointer"
              />
            </template>
            <template v-else>
              <img
                src="@/assets/generic/icons/ic-eye.svg"
                alt="eye-on"
                class="cursor-pointer"
              />
            </template>
          </div>
          </v-text-field>
          <!-- <div class="color-denotive-red-1 avenir-12-reg mt-1" v-if="notValid">Kata sandi salah</div> -->
        </div>
          <v-btn
            depressed
            color="primary"
            block
            height="48px"
            class="mt-4 button-login"
            @click="signUp()"
            :disabled="isDisabled || username === '' || password === ''"
          > Daftar
          </v-btn>
          <div class="action-login text-right pt-2 cursor-pointer" @click="routeToLogin()">
            Sudah punya akun? <span class="color-blue">masuk disini</span>
          </div>
      </div>
    </v-layout>
  </div>
</template>
<script>
export default {
  data: function () {
    return {
      email: '',
      username: '',
      password: '',
      showPassword: false,
      isDisabled: true,
      youtubeTitle: '',
      validation: {
        email: ''
      },
      notValid: false
    }
  },
  watch: {
    email (value) {
      if (value === '') {
        this.validation.email = ''
      } else {
        const pattern = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
        if (pattern.test(value) === false) {
          this.isDisabled = true
          this.validation.email = 'Format e-mail salah'
        } else {
          this.isDisabled = false
          this.validation.email = ''
        }
      }
    }
  },
  methods: {
    routeToLogin () {
      this.$router.push('/')
    },
    async signUp () {
      // this.$loading('start')
      try {
        const payload = {
          email: this.email,
          password: this.password,
          username: this.username
        }

        const res = await this.$store.dispatch('auth/signUp', payload)

        if (res.message === 'Proses save berhasil' || res.statusCode === 2000) {
          alert('Berhasil Register')
          this.routeToLogin()
        }
      } catch (err) {
        alert('Gagal Memproses')
        throw new Error(err)
      } finally {
        // this.$loading('finish')
      }
    }
  }
}
</script>
<style lang="sass">
.test-pages
  *
    margin: 0
    padding: 0

  .wrapper-login
    border: 1px solid #d3d3d3
    width: 50%
    position: absolute
    top: 50%
    left: 50%
    transform: translate(-50%, -50%)
    border-radius: 8px

  .button-login
    border-radius: 4px !important

  .action-login
    .color-blue
      color: #1976D2
</style>
